<template>
  <div v-if=" oilVersion >= 11 ">
    <Oil3011 />
  </div>
  <div v-else>
    <OilBefore3011 />
  </div>
</template>


<script setup>
/*
报警器 和 刷卡器 共用
*/
import OilBefore3011 from "@/components/Oil/OilBefore3011.vue";
import Oil3011 from "@/components/Oil/Oil3011.vue";
import { defineComponent, ref, onMounted } from "vue";
import { getVersion } from "@/utlis/VersionBranch"

import mixins from '@/mixins/index.js';
let {  postAN }= mixins();

const oilVersion = ref(0);

// 命名空间
defineComponent({
  name: "yunweibao-ToilLevel",
});


// 向安卓发送指令
const androidStatus_fn = () => {
  var version = postAN.ANVersion();
  getVersion(version, (msg) => {
    oilVersion.value = parseInt(msg);
  })
};

onMounted(() => {
  androidStatus_fn();
});
</script>

<style scoped>
.cell-group {
  margin: 10px;
}
.checkbox {
  margin-top: 0px;
  margin-bottom: 15px;
}
.trInfo {
  width: 30vh;
  text-align: center;
  height: 30px;
}
</style>